import React, { useEffect } from "react";

import { useStateValue } from "../../../StateProvider";

import "./Game.css";

function Game() {
  const [{ name }, setName] = useStateValue();
  const [{ avatar_index }, SetAvatarIndex] = useStateValue();

  const images = [
    "https://www.shareicon.net/data/512x512/2016/09/15/829446_user_512x512.png",
    "https://www.vippng.com/png/full/217-2177158_one-piece-round-request-monkey-d-by-monkey.png",
    "https://images.squarespace-cdn.com/content/v1/589e02a4893fc080a68bc8f1/1623619894555-S7D6O3IEOZZLPIB8EAK4/Profilepicture+%28round%29png.png",
    "https://image.flaticon.com/icons/png/512/146/146035.png",
  ];

  const renderAvatar = (param) => {
    switch (param) {
      case 0:
        return <img className="image_avatar_game" src={images[0]} />;
        break;
      case 1:
        return <img className="image_avatar_game" src={images[1]} />;
        break;
      case 2:
        return <img className="image_avatar_game" src={images[2]} />;
        break;
      case 3:
        return <img className="image_avatar_game" src= {images[3]} />;
        break;
      default:
        break;
    }
  };
  return <div className="game">
    <div className="game_header">
      <div className="basic_info">
      <h1 className = "game_header_user_name">Name: {name}</h1>
      <h4>Health: 80%</h4>
      <h4>Wealth: 1M$</h4>
      </div>
    {renderAvatar(avatar_index)}
    </div>
  </div>;
}

export default Game;
