import { Route, Router, Switch } from "react-router-dom";
import "./App.css";
import ChooseName from "./Views/Components/ChooseName/ChooseName";
import Game from "./Views/Components/Game/Game";
import { createMemoryHistory } from "history";

const history = createMemoryHistory();

function App() {
  return (
    <div className="app">
      <Router history={history}>
        <Switch>
          <Route exact path="/" component={ChooseName} />
          <Route exact path="/game" component={Game} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
