import React, { useState } from "react";
import "./ChooseName.css";
import { Link } from "react-router-dom";
import { Button } from "@material-ui/core";
import { actionTypes } from "../../../Reducer";
import { useStateValue } from "../../../StateProvider";

function ChooseName() {
  const [selected, setselected] = useState(0);

  const images = [
    'https://www.shareicon.net/data/512x512/2016/09/15/829446_user_512x512.png',
    'https://www.vippng.com/png/full/217-2177158_one-piece-round-request-monkey-d-by-monkey.png',
    'https://images.squarespace-cdn.com/content/v1/589e02a4893fc080a68bc8f1/1623619894555-S7D6O3IEOZZLPIB8EAK4/Profilepicture+%28round%29png.png',
    'https://image.flaticon.com/icons/png/512/146/146035.png',
  ]

  const [{ name }, setName] = useStateValue();
  const [{ avatar_index }, SetAvatarIndex] = useStateValue();

  const [user_name,set_user_name] = useState("")

  const SaveData = (e) =>{
    e.preventDefault();
    console.log("name of the user: ",user_name)
    setName({
      type: actionTypes.SET_USER_NAME,
      name : user_name,
    }
    )
    SetAvatarIndex({
      type: actionTypes.SET_AVATAR_INDEX,
      avatar_index:selected
    })
  }
  return (
    <div className="chooseName">
      <h1 className="chooseName_title">RICH SIMULATOR</h1>
      <div className="chooseName_inputName">
        <input placeholder="Name" onChange = {(e)=>set_user_name(e.target.value)}></input>
      </div>
      <h3>Choose Avatar</h3>
      <div className="chooseName_chooseAvatar">
        {[1, 2, 3, 4].map((item, index) => (
          <div
            className="avatar"
            key={index}
            onClick={() => setselected(index)}
          >
            {selected === index ? (
              <img className = "image_avatar_selected" src={images[index]} />

            ) : (
              <img className = "image_avatar" src={images[index]} />
            )}
          </div>
        ))}
      </div>
      <Button onClick = {(e)=>SaveData(e)} variant="outlined" color="secondary" size="large">
        <Link to="/game">Start!</Link>
      </Button>
    </div>
  );
}

export default ChooseName;
